import { Injectable, Logger, OnModuleInit, Inject } from '@nestjs/common';
import { Cron } from '@nestjs/schedule';
import { InjectQueue } from '@nestjs/bull';
import { Queue, Job } from 'bull';
import { WithdrawalService } from './withdrawal.service';
import { DeviceService } from '../device/device.service';
import { DeviceLockService } from '../device/device-lock.service';
import { WEBSOCKET_SERVICE } from '@/common/modules/websocket/constants/websocket';
import { IWebSocketService } from '@/common/modules/websocket/interface/websocket.interface';
import { Withdrawal } from '../../common/interfaces/withdrawal.interface';
import { WithdrawalProcessingError, ErrorType } from './withdrawal-errors';
import { cloneDeep } from 'lodash';
import { withCronLock } from '@/common/redis/cron-lock';
import { BankService } from '../bank/bank.service';
import { NotificationService } from '../notifiction/notification.service';
import { WithdrawalStatus } from './constants/withdrawal-status';

@Injectable()
export class WithdrawalJobManagerService implements OnModuleInit {
  private readonly logger = new Logger('WithdrawalJobManagerService');
  private processingJobs: Set<number | string> = new Set(); // Track job IDs being processed
  private isInitialized = false;

  constructor(
    @InjectQueue('wda-request') private readonly withdrawalQueue: Queue<Withdrawal>,
    private readonly withdrawalService: WithdrawalService,
    private readonly deviceService: DeviceService,
    private readonly deviceLock: DeviceLockService,
    @Inject(WEBSOCKET_SERVICE)
    protected readonly wsService: IWebSocketService,
    private readonly bankService: BankService,
    private readonly notificationService: NotificationService,
  ) { }

  async onModuleInit() {
    this.logger.log('WithdrawalJobManagerService initialized, waiting 5 seconds before starting...');
    // Wait a bit for other services to initialize
    setTimeout(() => {
      this.initializeJobProcessing();
    }, 5000);
  }

  /**
   * Initialize job processing - pick jobs equal to number of active devices
   */
  private async initializeJobProcessing() {
    if (this.isInitialized) {
      return;
    }

    await this.startProcessing();
  }

  /**
   * Start processing jobs - pick jobs equal to number of active devices
   * Can be called when service status changes from inactive to active
   */
  async startProcessing(): Promise<void> {
    try {
      this.logger.log('Starting withdrawal job processing...');

      await this.pickAndProcessJobs();

      this.isInitialized = true;
    } catch (error) {
      this.logger.error(`Error starting job processing: ${error.message}`, error.stack);
    }
  }

  /**
   * Get list of available devices (Active AND Unlocked)
   */
  private async getAvailableDevices(): Promise<Array<{ udid: string; config: any }>> {
    try {
      const devices = await this.wsService.listDevices();
      const availableDevices = [];

      for (const device of devices) {
        // 1. Check Config Status
        const deviceConfig = await this.deviceService.getDeviceConfig(device.udid);
        if (deviceConfig?.status !== 'active') {
          continue;
        }

        // 2. Check Lock Status (Must be free)
        const isLocked = await this.deviceLock.isLocked(device.udid);
        if (isLocked) {
          // this.logger.debug(`Device ${device.udid} is active but LOCKED. Skipping.`);
          continue;
        }

        availableDevices.push({ udid: device.udid, config: deviceConfig });
      }

      return availableDevices;
    } catch (error) {
      this.logger.error(`Error getting available devices: ${error.message}`);
      return [];
    }
  }

  /**
   * Pick and process jobs from queue
   * Strategy: Find N available devices -> Pick N jobs -> Assign strictly
   */
  async pickAndProcessJobs(count?: number): Promise<void> {
    try {

      if (!this.wsService.isConnectedStatus()) {
        this.logger.error(`WebSocket is not connected, cannot pick jobs`);
        // TODO: Notification on telegram
        return;
      }

      // Check service status
      const serviceStatus = await this.deviceService.getServiceStatus();
      if (serviceStatus !== 'active') {
        this.logger.debug(`Service is ${serviceStatus}, cannot pick jobs`);
        return;
      }

      // 1. Get Available Devices (Active + Unlocked)
      const availableDevices = await this.getAvailableDevices();

      // Filter out devices that are currently assigned to processing jobs (in-memory safeguard)
      // Note: DeviceLock check above should cover this, but this adds extra layer if lock is acquired later
      // Actually, since we process async, the lock might not be acquired yet if we just called processJob.
      // But we don't map job->device in `processingJobs`.
      // The best way is to rely on `getAvailableDevices` checking the Lock Service.

      // Calculate capacity
      const capacity = availableDevices.length;

      if (capacity <= 0) {
        // this.logger.debug(`No available (unlocked) devices. Waiting...`);
        return;
      }

      // 2. Pick Jobs
      // Number of jobs to pick = min(requested_count OR capacity, capacity)
      // Since each job needs 1 unique device.
      const jobsToPick = count ? Math.min(count, capacity) : capacity;

      // Get waiting and delayed jobs separately
      // getJobs(types, start, end): end is inclusive.
      // If we want N jobs, index is 0 to N-1
      const waitingJobs = await this.withdrawalQueue.getJobs(['waiting'], 0, jobsToPick - 1);


      if (waitingJobs.length === 0) {
        // this.logger.debug('No waiting jobs available in queue');
        return;
      }

      this.logger.log(
        `Found ${availableDevices.length} available devices. ` +
        `Picking ${waitingJobs.length} jobs (${(waitingJobs || []).length} waiting.`
      );

      // 3. Assign Job -> Device
      // We iterate through picked jobs and assign to available devices index-by-index
      for (let i = 0; i < waitingJobs.length; i++) {
        const job = waitingJobs[i];
        const device = availableDevices[i]; // Guaranteed to exist because jobsToPick <= capacity

        // Skip if already processing (safety check)
        if (this.processingJobs.has(job.id)) {
          continue;
        }

        // CRITICAL: Acquire lock immediately to prevent race condition
        const operationId = `withdraw:${job.data.withdrawalId}:${device.udid}`;
        const lockAcquired = await this.deviceLock.acquireLock(
          device.udid,
          'transfer',
          operationId,
          3.68 * 60 * 1000 // 3.68 minutes
        );

        if (!lockAcquired) {
          this.logger.warn(`Device ${device.udid} was locked after assignment, skipping job ${job.id}`);
          continue; // Skip this job, it will be picked up later
        }

        // Lock acquired - process job
        // Note: processWithdrawal will release the lock in its finally block
        this.processJob(job, device.udid)
          .catch(error => {
            // Safety: release lock if processJob fails before processWithdrawal
            this.deviceLock.releaseLock(device.udid).catch(() => { });
            this.logger.error(`Error processing job ${job.id}: ${error.message}`, error.stack);
          });
      }
    } catch (error) {
      this.logger.error(`Error picking jobs: ${error.message}`, error.stack);
    }
  }

  /**
   * Process a single job with assigned device
   */
  private async processJob(job: Job<Withdrawal>, assignedDeviceId: string): Promise<void> {
    const withdrawal = cloneDeep(job.data);
    const { _id, withdrawalId } = withdrawal;
    const deviceConfig = await this.deviceService.getDeviceConfig(assignedDeviceId);

    try {
      this.logger.log(`Successfully processed and removed withdrawal ${withdrawalId} from queue`);
      await job.remove();
    } catch (removeError) {
      this.logger.warn(`Could not remove completed job ${job.id}: ${removeError.message}`);
    }

    this.notificationService
      .updateWithdrawalStatus(_id, WithdrawalStatus.PROCESSING)
      .catch((error) => {
        this.logger.error(`Error updating withdrawal status for ${withdrawalId}: ${error.message}`, error.stack);
      });

    if (!withdrawal || !withdrawal.withdrawalId) {
      this.logger.error(`Invalid withdrawal data in job ${job.id}`);
    }

    // Generate QR code from transferCode if available
    if (!withdrawal.transferCode) {
      throw new WithdrawalProcessingError(`Transfer code is required for withdrawal ${withdrawalId}`, ErrorType.PERMANENT);
    }

    await this.bankService.createQRCodeAndTransferTo(deviceConfig.bankCode, assignedDeviceId, withdrawal);

    // Mark as processing
    this.processingJobs.add(job.id);

    try {
      await this.withdrawalService.processWithdrawal(withdrawal, assignedDeviceId);
    } catch (error) {
      // EXCEPTION = FAILED
      this.logger.error(`Error processing withdrawal ${withdrawalId}: ${error.message}`, error.stack);

      // Extract error type t error nu c
      let errorType: ErrorType;

      if (error instanceof WithdrawalProcessingError) {
        errorType = error.errorType;
      }

      if(errorType === ErrorType.FORCE_LOGIN) {
        await this.bankService.login(deviceConfig.bankCode, assignedDeviceId, true);
      }

      if (errorType === ErrorType.NO_BILL_AFTER_OTP) {
        await this.bankService.captureScreen(deviceConfig.bankCode, assignedDeviceId, withdrawal.withdrawalCode);
        const billImagePath = await this.bankService.getBillImagePath(deviceConfig.bankCode, withdrawal.withdrawalCode);

        if (billImagePath) {
          this.logger.log(`Uploading receipt for withdrawal ${withdrawal.withdrawalCode} - ${billImagePath}`);

          await this.notificationService.uploadReceipt(withdrawal._id, billImagePath);
        }

        await this.notificationService.updateWithdrawalStatus(withdrawal._id, WithdrawalStatus.UNKNOWN);

        await this.bankService.login(deviceConfig.bankCode, assignedDeviceId, true);
      }

      await this.handleJobFailure(withdrawal, error.message, errorType);
    } finally {
      // Remove from processing set
      this.processingJobs.delete(job.id as number);
    }
  }

  /**
   * Handle Job Failure: Retry logic with error type classification
   */
  private async handleJobFailure(
    withdrawal: Withdrawal,
    reason: string,
    errorType: ErrorType = ErrorType.PERMANENT
  ) {
    const maxRetries = 1;
    const currentRetries = withdrawal.manualRetryCount ?? 0;

    this.logger.warn(
      `Withdrawal ${withdrawal.withdrawalId} failed. ` +
      `Reason: ${reason}. ` +
      `Error Type: ${errorType}. ` +
      `Retry attempt: ${currentRetries}/${maxRetries}`
    );

    if (currentRetries < maxRetries) {
      if (
        errorType === ErrorType.PERMANENT
      ) {
        return;
      }

      await this.withdrawalQueue.add(
        'process-withdrawal-request',
        {
          ...withdrawal,
          manualRetryCount: currentRetries + 1,
          lastErrorType: errorType,
          lastErrorReason: reason
        },
        {
          attempts: 0,
          removeOnComplete: false,
          removeOnFail: false,
          jobId: `withdrawal-request-${withdrawal.withdrawalId}`,
          priority: 0,
          delay: 30000, // 30s
        }
      );
    } else {
      this.logger.warn(`Max retries (${maxRetries}) exceeded for withdrawal ${withdrawal.withdrawalId}: ${errorType} error: ${reason}`);
    }
  }

  private async promoteDelayedJobs() {
    return withCronLock('cron:promote_delayed:withdrawal', 5000, async () => {
      const now = Date.now();
      const BATCH = 100;

      // Ly mt batch job  delayed
      const delayedJobs: Job[] = await this.withdrawalQueue.getJobs(
        ['delayed'],
        0,
        BATCH - 1,
      );

      if (!delayedJobs.length) return;

      let promoted = 0;

      for (const job of delayedJobs) {
        const delayMs = typeof job.opts?.delay === 'number' ? job.opts.delay : 0;
        const ts = typeof job.timestamp === 'number' ? job.timestamp : 0;

        const dueAt = ts + delayMs;

        // ch promote job n hn
        if (dueAt <= now) {
          try {
            // Bull h tr job.promote()  chuyn delayed -> waiting
            await job.promote();
            promoted++;
          } catch (e) {
            this.logger.debug(`Promote skipped for job ${job.id}: ${String(e)}`);
          }
        }
      }

      if (promoted > 0) {
        this.logger.log(`Promoted ${promoted} delayed jobs to waiting`);
      }
    });
  }

  /**
   * Manually trigger job picking (for testing or manual operations)
   */
  async triggerPickJobs(count: number = 1): Promise<void> {
    await this.pickAndProcessJobs(count);
  }

  /**
   * Get queue statistics
   */
  async getQueueStats() {
    // Need to implement new stats logic if needed, but keeping existing is fine for now
    // availableDevices logic changed slightly
    const [waiting, delayed, active, completed, failed, availableDevices, serviceStatus] = await Promise.all([
      this.withdrawalQueue.getWaitingCount(),
      this.withdrawalQueue.getDelayedCount(),
      this.withdrawalQueue.getActiveCount(),
      this.withdrawalQueue.getCompletedCount(),
      this.withdrawalQueue.getFailedCount(),
      this.getAvailableDevices(),
      this.deviceService.getServiceStatus(),
    ]);

    return {
      serviceStatus,
      activeDevices: availableDevices.length, // Now explicitly unlocked active devices
      processingJobs: this.processingJobs.size,
      isInitialized: this.isInitialized,
      queue: {
        waiting,
        delayed,
        active,
        completed,
        failed,
        total: waiting + delayed + active,
      },
    };
  }

  /**
   * Debug method: Get detailed queue information
   */
  async debugQueue() {
    const stats = await this.getQueueStats();
    const [waitingJobs, delayedJobs] = await Promise.all([
      this.withdrawalQueue.getJobs(['waiting'], 0, 9), // Get first 10 waiting jobs
      this.withdrawalQueue.getJobs(['delayed'], 0, 9), // Get first 10 delayed jobs
    ]);

    return {
      ...stats,
      sampleWaitingJobs: waitingJobs.map(job => ({
        id: job.id,
        data: job.data,
        opts: job.opts,
        manualRetryCount: (job.data as any).manualRetryCount
      })),
      sampleDelayedJobs: delayedJobs.map(job => ({
        id: job.id,
        data: job.data,
        opts: job.opts,
        manualRetryCount: (job.data as any).manualRetryCount
      })),
    };
  }

  /**
   * Periodically check and pick jobs if there are available slots
   * Runs every 5 seconds
   */
  @Cron('*/5 * * * * *')
  async periodicJobPicking() {
    await this.pickAndProcessJobs();
  }

  /**
   * Promote delayed jobs to waiting jobs
   * Runs every 5 seconds
   */
  @Cron('*/5 * * * * *')
  async promoteDelayedJobsPeriodically() {
    await this.promoteDelayedJobs();
  }
}
