import { Injectable, Logger, OnModuleInit } from '@nestjs/common';
import { RedisService } from '../../common/services/redis.service';
import { HcBoxService } from '../hcbox/hcbox.service';
import { DeviceConfig, UpdateDeviceConfigDto } from './dto/device-config.dto';

@Injectable()
export class DeviceService implements OnModuleInit {
  private readonly logger = new Logger('DeviceService');
  private readonly REDIS_DEVICE_CONFIG_PREFIX = 'device:';
  private readonly REDIS_DEVICE_CONFIG_SUFFIX = ':config';
  private readonly REDIS_SERVICE_STATUS_KEY = 'service:status';

  constructor(
    private readonly redisService: RedisService,
    private readonly hcBoxService: HcBoxService,
  ) {}

  async onModuleInit() {
    // Always set service status to 'inactive' on server startup
    try {
      await this.setServiceStatus('inactive');
      this.logger.log('Service status initialized to: inactive');
    } catch (error) {
      this.logger.error(`Failed to initialize service status: ${error.message}`);
    }
  }

  async listDevices() {
    try {
      // Get devices from HC BOX service
      const devices = await this.hcBoxService.listDevices();
      
      // Map bankCode and config from Redis for each device
      // BankCode mapping is stored in Redis: device:{deviceId}:config
      const devicesWithConfig = await Promise.all(
        devices.map(async (device: any) => {
          // Get saved config from Redis (includes bankCode mapping)
          const config = await this.getDeviceConfig(device.udid);
          
            return {
            ...device,
            // Map bankCode from Redis config (persistent mapping)
            bankCode: config?.bankCode || '',
            availableAmount: config?.availableAmount || 0,
            status: config?.status || 'deactive',
            password: config?.password || '',
            smartOTP: config?.smartOTP || '',
            accountNo: config?.accountNo || '',
            accountName: config?.accountName || '',
          };
        }),
      );

      return devicesWithConfig;
    } catch (error) {
      this.logger.error(`Error listing devices: ${error.message}`);
      throw error;
    }
  }

  async getDeviceConfig(deviceId: string): Promise<DeviceConfig | null> {
    try {
      const key = `${this.REDIS_DEVICE_CONFIG_PREFIX}${deviceId}${this.REDIS_DEVICE_CONFIG_SUFFIX}`;
      const data = await this.redisService.get(key);
      
      if (!data) {
        return null;
      }

      return JSON.parse(data) as DeviceConfig;
    } catch (error) {
      this.logger.error(`Error getting device config for ${deviceId}: ${error.message}`);
      return null;
    }
  }

  async updateDeviceConfig(deviceId: string, dto: UpdateDeviceConfigDto): Promise<DeviceConfig> {
    try {
      const key = `${this.REDIS_DEVICE_CONFIG_PREFIX}${deviceId}${this.REDIS_DEVICE_CONFIG_SUFFIX}`;
      
      // Get existing config or create new
      const existing = await this.getDeviceConfig(deviceId);
      const config: DeviceConfig = {
        deviceId,
        // BankCode mapping: save once, persist in Redis
        bankCode: dto.bankCode !== undefined ? dto.bankCode : existing?.bankCode || '',
        availableAmount: dto.availableAmount !== undefined ? dto.availableAmount : existing?.availableAmount || 0,
        status: dto.status !== undefined ? dto.status : existing?.status || 'deactive',
        password: dto.password !== undefined ? dto.password : existing?.password || '',
        smartOTP: dto.smartOTP !== undefined ? dto.smartOTP : existing?.smartOTP || '',
        accountNo: dto.accountNo !== undefined ? dto.accountNo : existing?.accountNo || '',
        accountName: dto.accountName !== undefined ? dto.accountName : existing?.accountName || '',
      };

      // Save to Redis (no expiration - persistent config including bankCode mapping)
      // Key format: device:{deviceId}:config
      await this.redisService.set(key, JSON.stringify(config));

      this.logger.log(`[${deviceId}] Updated device config (bankCode: ${config.bankCode || 'not set'}): ${JSON.stringify(config)}`);
      return config;
    } catch (error) {
      this.logger.error(`[${deviceId}] Error updating device config: ${error.message}`);
      throw error;
    }
  }

  async getServiceStatus(): Promise<'active' | 'inactive'> {
    try {
      const status = await this.redisService.get(this.REDIS_SERVICE_STATUS_KEY);
      return (status as 'active' | 'inactive') || 'inactive';
    } catch (error) {
      this.logger.error(`Error getting service status: ${error.message}`);
      return 'inactive';
    }
  }

  async setServiceStatus(status: 'active' | 'inactive'): Promise<void> {
    try {
      await this.redisService.set(this.REDIS_SERVICE_STATUS_KEY, status);
      this.logger.log(`Service status set to: ${status}`);
    } catch (error) {
      this.logger.error(`Error setting service status: ${error.message}`);
      throw error;
    }
  }

  async deductDeviceAmount(deviceId: string, amount: number): Promise<boolean> {
    try {
      const config = await this.getDeviceConfig(deviceId);
      if (!config) {
        return false;
      }

      const newAmount = config.availableAmount - amount;
      if (newAmount < 0) {
        this.logger.warn(`Insufficient amount for device ${deviceId}. Available: ${config.availableAmount}, Requested: ${amount}`);
        return false;
      }

      await this.updateDeviceConfig(deviceId, { availableAmount: newAmount });
      return true;
    } catch (error) {
      this.logger.error(`Error deducting amount for device ${deviceId}: ${error.message}`);
      return false;
    }
  }

  /**
   * Check if there is at least one device with status = 'active'
   */
  async hasActiveDevice(): Promise<boolean> {
    try {
      const devices = await this.listDevices();
      return devices.some(device => device.status === 'active');
    } catch (error) {
      this.logger.error(`Error checking active devices: ${error.message}`);
      return false;
    }
  }
}

