import { Controller, Get, Put, Param, Body, Post, Render, HttpException, HttpStatus, Logger } from '@nestjs/common';
import { DeviceService } from './device.service';
import { UpdateDeviceConfigDto } from './dto/device-config.dto';
import { BankService } from '../bank/bank.service';
import { config } from '@/common/constants/config';

@Controller()
export class DeviceController {
  private readonly logger = new Logger('DeviceController');

  constructor(
    private readonly deviceService: DeviceService,
    private readonly bankService: BankService,
  ) { }

  @Get()
  @Render('devices')
  async getDevicesPage() {
    const devices = await this.deviceService.listDevices();
    const serviceStatus = await this.deviceService.getServiceStatus();

    return {
      devices,
      serviceStatus,
      banks: ['VIETCOMBANK', 'TPBANK', 'TECHCOMBANK', 'ACB', 'PGBANK'],
      boxType: config.boxType,
    };
  }

  @Get('api/devices')
  async getDevices() {
    const devices = await this.deviceService.listDevices();
    return {
      success: true,
      data: devices,
    };
  }

  @Get('api/devices/:deviceId/config')
  async getDeviceConfig(@Param('deviceId') deviceId: string) {
    const config = await this.deviceService.getDeviceConfig(deviceId);
    return {
      success: true,
      data: config,
    };
  }

  @Post('api/devices/:deviceId/retryLogin')
  async retryLogin(@Param('deviceId') deviceId: string) {
    try {
      this.logger.log(`Retry login requested for device ${deviceId}`);

      // Get device config to check bankCode and status
      const deviceConfig = await this.deviceService.getDeviceConfig(deviceId);

      if (!deviceConfig) {
        throw new HttpException(
          {
            success: false,
            error: `Device ${deviceId} not found or not configured`,
          },
          HttpStatus.BAD_REQUEST,
        );
      }

      if (!deviceConfig.bankCode || deviceConfig.bankCode.trim() === '') {
        throw new HttpException(
          {
            success: false,
            error: `Device ${deviceId} does not have bankCode configured. Please configure bankCode first.`,
          },
          HttpStatus.BAD_REQUEST,
        );
      }

      if (deviceConfig.status !== 'active') {
        throw new HttpException(
          {
            success: false,
            error: `Device ${deviceId} is not active. Please activate the device first.`,
          },
          HttpStatus.BAD_REQUEST,
        );
      }

      // Attempt to login
      const bankCode = deviceConfig.bankCode.toUpperCase();
      await this.bankService.login(bankCode, deviceId);

      return {
        success: true,
        message: `Successfully logged in to ${bankCode} on device ${deviceId}`,
      };
    } catch (error) {
      if (error instanceof HttpException) {
        throw error;
      }
      this.logger.error(`Error retrying login for device ${deviceId}: ${error.message}`, error.stack);
      throw new HttpException(
        {
          success: false,
          error: error.message || 'Failed to retry login',
        },
        HttpStatus.INTERNAL_SERVER_ERROR,
      );
    }
  }

  @Put('api/devices/:deviceId/config')
  async updateDeviceConfig(
    @Param('deviceId') deviceId: string,
    @Body() dto: UpdateDeviceConfigDto,
  ) {
    try {
      this.logger.log(`Updating device config for ${deviceId}: ${JSON.stringify(dto)}`);

      // Get existing config to check current status if status is not provided
      const existingConfig = await this.deviceService.getDeviceConfig(deviceId);
      const finalStatus = dto.status !== undefined ? dto.status : existingConfig?.status || 'deactive';

      // Manual validation: if status is 'active' (either from dto or existing), all fields are required
      if (finalStatus === 'active') {
        const errors: string[] = [];

        // Check bankCode: from dto or existing config
        const bankCode = dto.bankCode !== undefined ? dto.bankCode : existingConfig?.bankCode;
        if (!bankCode || bankCode.trim() === '') {
          errors.push('Bank Code l bt buc khi status l active');
        }

        // Check availableAmount: from dto or existing config
        const availableAmount = dto.availableAmount !== undefined ? dto.availableAmount : existingConfig?.availableAmount;
        if (availableAmount === undefined || availableAmount === null || availableAmount < 0) {
          errors.push('Available Amount l bt buc v phi >= 0 khi status l active');
        }

        // Check password: from dto or existing config
        const password = dto.password !== undefined ? dto.password : existingConfig?.password;
        if (!password || password.trim() === '') {
          errors.push('Password l bt buc khi status l active');
        }

        // Check smartOTP: from dto or existing config
        const smartOTP = dto.smartOTP !== undefined ? dto.smartOTP : existingConfig?.smartOTP;
        if (!smartOTP || smartOTP.trim() === '') {
          errors.push('Smart OTP l bt buc khi status l active');
        }

        if (errors.length > 0) {
          throw new HttpException(
            {
              success: false,
              error: errors.join(', '),
            },
            HttpStatus.BAD_REQUEST,
          );
        }
      }

      const config = await this.deviceService.updateDeviceConfig(deviceId, dto);
      return {
        success: true,
        data: config,
      };
    } catch (error) {
      if (error instanceof HttpException) {
        throw error;
      }
      this.logger.error(`Error updating device config for ${deviceId}: ${error.message}`, error.stack);
      throw new HttpException(
        {
          success: false,
          error: error.message || 'Failed to update device config',
        },
        HttpStatus.INTERNAL_SERVER_ERROR,
      );
    }
  }

  @Get('api/service/status')
  async getServiceStatus() {
    const status = await this.deviceService.getServiceStatus();
    return {
      success: true,
      data: { status },
    };
  }

  @Post('api/service/start')
  async startService() {
    try {
      // Check if there is at least one device with status = 'active'
      const hasActiveDevice = await this.deviceService.hasActiveDevice();

      if (!hasActiveDevice) {
        throw new HttpException(
          {
            success: false,
            error: 'Vui lng active bank trc khi start',
          },
          HttpStatus.BAD_REQUEST,
        );
      }

      // Login all active banks (Non-blocking: don't await completion)
      // This allows devices to start working individually as soon as they finish login
      this.bankService.loginAllActiveBanks().catch(err => {
        this.logger.error(`Error during background login process: ${err.message}`);
      });

      // Give a small buffer for logins to acquire locks before enabling service
      await new Promise(resolve => setTimeout(resolve, 2000));

      // Set service status to active
      await this.deviceService.setServiceStatus('active');

      // Trigger job processing immediately
      // (Optional: fetch JobManager and trigger? But cron will pick it up in <10s)

      return {
        success: true,
        message: 'Service started successfully. Devices are logging in and will accept jobs when ready.',
      };
    } catch (error) {
      if (error instanceof HttpException) {
        throw error;
      }
      this.logger.error(`Error starting service: ${error.message}`, error.stack);
      throw new HttpException(
        {
          success: false,
          error: error.message || 'Failed to start service',
        },
        HttpStatus.INTERNAL_SERVER_ERROR,
      );
    }
  }

  @Post('api/service/stop')
  async stopService() {
    await this.deviceService.setServiceStatus('inactive');
    return {
      success: true,
      message: 'Service stopped successfully',
    };
  }
}

